body {
    font-family: 'Roboto', sans-serif;
}

.app {
    padding-top: 5rem;
    display: flex;
    justify-content: center;
}

.wrap {
    width: 500px;
}

$height: 46px;

.select {
    width: 100%;
    position: relative;
    
    &.open {
        .select__dropdown {
            display: block;
        }

        .select__input {
            border-bottom: none;
        }
    }

    &__input {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: $height;
        padding: 0 1rem;

        border: 1px solid #ccc;
        border-radius: 5px;
        cursor: pointer;
    }

    
    &__dropdown {
        // display: none;
        position: absolute;
        border: 1px solid #ccc;
        border-radius: 5px;
        top: $height;
        left: 0;
        right: 0;
        max-height: 200px;
        overflow-y: scroll;
        box-shadow: 2px 3px 5px rgba(0, 0, 0, .3);
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;
    }

    &__item {
        padding: 1rem;
        border-bottom: 1px solid #ccc;

        &:hover {
            background-color: #eee;
            cursor: pointer;
            transition: .15s background-color ease-in;
        }
    }
}